.hero-section {
    position: relative; 
    height: 100vh;
    width: 100%;
    padding-top: 40vh;
    /* display: flex;
    justify-content: center;
    align-items: center; */
}

.hero-section::before {    
      content: "";
      background-image: url('./hero-section.png');
      background-size: cover;
      background-repeat: no-repeat;
      position: absolute;
      top: 0px;
      right: 0px;
      bottom: 0px;
      left: 0px;
      opacity: 0.65;
}

.h1,.h2 {
  position: relative;
  color: #000000c9;  
  font-size: 4rem;
  font-family: 'Bebas Neue', sans-serif;
  line-height: 0.9;
  display: flex;
    justify-content: center;
    align-items: center;
  text-align: center;
}

