.p1,
.p2 {
    width: 50%;
    color: black;
    font-weight: 1000;
    font-family: 'Rubik Doodle Shadow', system-ui;
    font-size: 30px;
    border: 5px solid black;
    height: 680px;
    display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.h1,.p{
    text-align: center;
}

.p1 {
    background-color: rgba(255, 0, 0, 0.6);
    padding: 80px;
}

.p2 {
    background-color: rgba(0, 0, 255, 0.623);
    padding: 101.37px;
}

.container1 {
    min-height: 40px;
    /* width: 100%; */
    text-align: center;
    padding: 40px 30px;
    font-size: xx-large;
    background-color: rgb(233, 65, 248);
    color: black;
    font-family: 'Rubik Doodle Shadow', system-ui;
    font-weight: 1000;
    font-size: 30px;
    border: 5px solid black;
}

.box1{
    display: flex;
    align-items: center;
    justify-content: center;
}

