@media (max-width: 800px) {
    body {
        overflow-x: hidden; /* Hide horizontal scrollbar */
    }


    .h1,
    .h2,
    .p {
        font-size: 1.5rem; /* Adjust the font size for smaller screens */
    }

    .container1 {
        padding: 10px; /* Adjust the padding for smaller screens */
        font-size: large; /* Adjust the font size for smaller screens */
    }

    .container2p1 {
        flex-direction: column;
    }

    .img2,
    .img3 {
        margin-top: 2px; /* Adjust the margin for smaller screens */
        margin-bottom: 35px;
    }

    .footer {
        margin-top: 10px; /* Adjust the margin for smaller screens */
    }
}
